//
import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
import store from '@/store/index'

//
//home  
import Home from '@/pages/Home/Home.vue'
import Search from '@/pages/Search/Search.vue'
import Login from '@/pages/Login'
import Register from '@/pages/Register'
import Detail from '@/pages/Detail'
import AddCartSuccess from '@/pages/AddCartSuccess'
import ShopCart from '@/pages/ShopCart'
import Trade from '@/pages/Trade'
import Pay from '@/pages/Pay'
import PaySuccess from '@/pages/PaySuccess'
import Center from '@/pages/Center'
import MyOrder from '@/pages/Center/myOrder'
import GroupOrder from '@/pages/Center/groupOrder'


let originPush = VueRouter.prototype.push;
let originReplace = VueRouter.prototype.replace;

//push|replace NavigationDuplicated
VueRouter.prototype.push = function (location, resolve, reject) {
    if (resolve && reject) {
        originPush.call(this, location, resolve, reject)
    } else {
        originPush.call(this, location, () => { }, () => { })
    }
}
//   originPush.call(this, location).catch(err=>err)
//replace
VueRouter.prototype.replace = function (location, resolve, reject) {
    originReplace.call(this, location).catch(err => err)
}

//
let router = new VueRouter({
    routes: [
        {
            name: "center",
            path: "/center",
            component: Center,
            meta: { show: true },
            children: [
                {
                    name: 'myorder',
                    path: 'myorder',
                    component: MyOrder,
                },
                {
                    name: 'grouporder',
                    path: 'grouporder',
                    component: GroupOrder,
                },
                {
                    path: '/center',
                    redirect: '/center/myorder'
                }
            ]
        },
        {
            name: "paysuccess",
            path: "/paysuccess",
            component: PaySuccess,
            meta: { show: true },
        },
        {
            name: "pay",
            path: "/pay",
            component: Pay,
            meta: { show: true },
        },
        {
            name: "trade",
            path: "/trade",
            component: Trade,
            meta: { show: true },
            //
            beforeEnter(to,from,next){
                //shopcarttrade
                // '/trade'  router,trade
                if(from.path == "/shopcart" || '/trade'){
                    next()
                } else {
                    //urlfrom  
                    next(false)
                }
            }
        },
        {
            name: "shopcart",
            path: "/shopcart",
            component: ShopCart,
            meta: { show: true },
        },
        {
            name: "addcartsuccess",
            path: "/addcartsuccess",
            component: AddCartSuccess,
            meta: { show: true },
        },
        {
            name: "detail",
            path: "/detail/:skuid",
            component: Detail,
            meta: { show: true },
        },
        {
            name: "home",
            path: "/home",
            // promise
            component: () => import('@/pages/Home/Home.vue'),
            //show footer
            meta: { show: true },
        },
        {
            name: "search",
            // params 
            path: "/search/:keyword?",
            component: Search,
            meta: { show: true },
            // props: {a:1,b:2}      
        },
        {
            name: "Login",
            path: "/login",
            component: Login,
        },
        {
            name: "Register",
            path: "/register",
            component: Register,
        },
        ///
        {
            path: '*',
            redirect: 'home'
        }
    ],
    // 
    scrollBehavior(to, from, savedPosition) {
        return { y: 0 }
    }
})

//  
router.beforeEach(async (to, from, next) => {
    //
    // let forbitRouter = ['/trade', '/pay', '/center']

    //localstoragestoretokenundefined
    // let token = localStorage.getItem("TOKEN")
    let token = store.state.user.token;
    let username = store.state.user.userInfo.name;
    //
    if (token) {
        //login
        if (to.path == '/login') {
            next('/home')
        } else {
            //,
            //
            if (username) {
                next();
            } else {
                //
                //
                try {
                    await store.dispatch('user/getUserInfo');
                    next();
                } catch (error) {
                    //token
                    await store.dispatch('user/userLogout');
                    next('/login')
                }
            }

        }
    } else {
        //,,
        //meta  token
        let toPath = to.path
        if (toPath.indexOf('/trade') !== -1 || toPath.indexOf('/pay') !== -1 || toPath.indexOf('/center') !== -1) {
            //
            next('/login?redirect='+toPath)
        } else {
            next()
        }

    }
})


export default router