//vee-validate 
import Vue from 'vue'
import VeeValidate from 'vee-validate'
//
import zh_CN from 'vee-validate/dist/locale/zh_CN'
Vue.use(VeeValidate)

// 
VeeValidate.Validator.localize('zh_CN', {
    messages: {
        ...zh_CN.messages,
        is: (field) => `${field}`  //message,    
    },
    attributes: {           //field
        phone: '',
        code: '',
        password: '',
        password1: '',
        agree: '',
    }
})

//
VeeValidate.Validator.extend("agree", {
    validate: (value) => {
        return value;
    },
    getMessage: (field) => field + "",
    
})