<template>
  <div class="register-container">
    <!--  -->
    <div class="register">
      <h3>
        
        <span class="go"
          > <a href="login.html" target="_blank"></a>
        </span>
      </h3>
      <div class="content">
        <label>:</label>
        <input
          type="text"
          placeholder=""
          v-model="phone"
          name="phone"
          v-validate="{
            required: true,
            regex:
              /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/,
          }"
          :class="{ invalid: errors.has('phone') }"
        />
        <span class="error-msg">{{ errors.first("phone") }}</span>
      </div>
      <div class="content">
        <label>:</label>
        <input
          type="text"
          placeholder=""
          v-model="code"
          name="code"
          v-validate="{
            required: true,
            regex: /^\d{6}$/,
          }"
          :class="{ invalid: errors.has('code') }"
        />
        <button style="width: 100px; height: 30px" @click="getCode(phone)">
          
        </button>
        <span class="error-msg">{{ errors.first("code") }}</span>
      </div>
      <div class="content">
        <label>:</label>
        <input
          type="password"
          placeholder=""
          v-model="password"
          name="password"
          v-validate="{
            required: true,
            regex: /^[0-9a-zA-Z]{6,20}$/,
          }"
          :class="{ invalid: errors.has('password') }"
        />
        <span class="error-msg">{{ errors.first("password") }}</span>
      </div>
      <div class="content">
        <label>:</label>
        <input
          type="password"
          placeholder=""
          v-model="password1"
          name="password1"
          v-validate="{
            required: true,
            is: password,
          }"
          :class="{ invalid: errors.has('password1') }"
        />
        <span class="error-msg">{{ errors.first("password1") }}</span>
      </div>
      <div class="controls">
        <input
          name="agree"
          type="checkbox"
          :checked="agree"
          v-validate="{
            required: true,
            agree: true,
          }"
          :class="{ invalid: errors.has('agree') }"
        />
        <span></span>
        <span class="error-msg">{{ errors.first("agree") }}</span>
      </div>
      <div class="btn">
        <button @click="userRegister"></button>
      </div>
    </div>

    <!--  -->
    <div class="copyright">
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
      <div class="address">6</div>
      <div class="beian">ICP19006430</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    return {
      // --
      phone: "",
      //
      code: "",
      //
      password: "",
      //
      password1: "",
      //
      agree: true,
    };
  },
  methods: {
    async getCode() {
      try {
        const { phone } = this;
        phone && (await this.$store.dispatch("user/getCode", phone));
        this.code = this.$store.state.user.code;
      } catch (error) {
        alert("");
      }
    },
    async userRegister() {
      // 
      const success = await this.$validator.validateAll();
      if (success) {
        try {
          await this.$store.dispatch("user/userRegister", {
            phone,
            code,
            password,
          });
          this.$router.push("/login");
        } catch (error) {
          alert("");
        }
      }
    },
  },
};
</script>

